<template>
  <div class="pageHeader">
    <div class="pageHeader-title" :style="{fontSize:titleSize+'px'}">
      {{ title }}
      <div class="pageHeader-subtitle" :style="{fontSize:subtitleSize+'px'}">{{ subtitle }}</div>
    </div>
    <div class="pageHeader-break"></div>
  </div>
</template>

<script>
export default {
  name: "PageHeader",
  props: {
    title: String,
    subtitle: String,
    titleSize: {
      type: Number,
      default: 60
    },
    subtitleSize: {
      type: Number,
      default: 36
    }
  }
};
</script>

<style scoped>
@keyframes expand {
  from {
    opacity: 0.1;
    width: 0;
  }
}

.pageHeader {
  display: inline-block;
  margin-bottom: 70px;
}
.pageHeader-title {
  font-size: 78px;
  font-weight: bold;
}
.pageHeader-subtitle {
  font-size: 43px;
  font-weight: normal;
}
.pageHeader-break {
  width: 250px;
  margin-top: 75px;
  border-bottom: 4px solid rgb(104, 111, 180);
  opacity: 1;
  animation: expand 0.5s ease-in;
}
</style>
