<template>
  <div>
    <Page :sections="sections">
      <Who v-show="isSelected('who')"/>
      <Goals v-show="isSelected('goals')"/>
      <Drives v-show="isSelected('drives')"/>
    </Page>
  </div>
</template>

<script>
import Page from "@/components/Page";
import Who from "./Who";
import Goals from "./Goals";
import Drives from "./Drives";

export default {
  name: "About",
  components: {
    Page,
    Who,
    Goals,
    Drives
  },
  methods: {
    isSelected(id) {
      return this.$store.state.selectedSectionId === id;
    }
  },
  computed: {
    sections() {
      return this.$store.state.pageSections["about"];
    }
  }
};
</script>

