<template>
  <div>
    <Page :sections="sections">
      <Welcome v-show="isSelected('welcome')"/>
      <Purpose v-show="isSelected('purpose')"/>
      <Organized v-show="isSelected('organized')"/>
    </Page>
  </div>
</template>

<script>
import Page from "@/components/Page";
import Welcome from "./Welcome";
import Purpose from "./Purpose";
import Organized from "./Organized";

export default {
  name: "Home",
  components: {
    Page,
    Welcome,
    Purpose,
    Organized
  },
  methods: {
    isSelected(id) {
      return this.$store.state.selectedSectionId === id;
    }
  },
  computed: {
    sections() {
      return this.$store.state.pageSections["home"];
    }
  }
};
</script>

