<template v-if="this.$store.pageSections">
  <div id="app">
    <MainHeader/>
    <PageLinks/>
    <Home v-show="isPageSelected('home')"/>
    <About v-show="isPageSelected('about')"/>
    <MainFooter/>
  </div>
</template>

<script>
import store from "@/store";
import PageLinks from "@/components/PageLinks";
import MainHeader from "@/components/MainHeader";
import MainFooter from "@/components/MainFooter";
import Home from "@/pages/home/index";
import About from "@/pages/about/index";

export default {
  name: "App",
  components: {
    PageLinks,
    MainHeader,
    MainFooter,
    Home,
    About
  },
  methods: {
    isPageSelected(id) {
      return this.$store.state.selectedPageId === id;
    }
  },
  store
};
</script>

