<template>
  <div>
    <div class="pageSections">
      <div
        v-for="(section, key) in sections"
        :key="key"
        :class="{'pageSections-item':true, 'selected':isSelected(section.id)}"
        @click="updateSection(section.id)"
      >
        <span class="pageSections-item-text">{{ section.name }}</span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "PageSections",
  props: ["sections"],
  methods: {
    isSelected(id) {
      return this.$store.state.selectedSectionId === id;
    },
    updateSection(id) {
      this.$store.state.selectedSectionId = id;
    }
  }
};
</script>

<style scoped>
@keyframes expand {
  from {
    height: 0;
  }
}

@keyframes slide-in {
  0% {
    transform: translate(-150%);
  }
  10% {
    transform: translate(-150%);
  }
}

.pageSections {
  display: inline-block;
  width: 280px;
  height: calc(100vh - 80px);
  margin-left: 90px;
  background: rgb(37, 38, 50);
  animation: slide-in 1.3s ease-out;
}
.pageSections-item {
  position: relative;
  display: inline-block;
  width: 100%;
  height: 40px;
  float: left;
}
.pageSections-item:nth-child(even) {
  background: rgba(255, 255, 255, 0.02);
}
.pageSections-item:hover {
  cursor: pointer;
  background: rgba(255, 255, 255, 0.03);
}
.pageSections-item.selected {
  background: rgb(26, 26, 35);
}
.pageSections-item.selected::before {
  content: "";
  position: absolute;
  left: 0;
  top: 50%;
  transform: translate(0, -50%);
  height: 100%;
  width: 3px;
  background: rgb(102, 109, 180);
  animation: expand 0.3s ease-out;
}
.pageSections-item-text {
  position: absolute;
  width: 45%;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  font-size: 11px;
  pointer-events: none;
}
</style>
