<template>
  <div>
    <div class="pageLinks">
      <a
        v-for="(link, key) in links"
        :key="key"
        :href="link.url"
        :class="{'pageLinks-item':true, 'selected':isSelected(link.id)}"
        @click="updatePage(link.id)"
      >
        <span class="pageLinks-item-text">{{ link.name }}</span>
      </a>
    </div>
  </div>
</template>

<script>
const links = [
  {
    name: "HOME",
    id: "home",
    url: "/#/"
  },
  {
    name: "ABOUT",
    id: "about",
    url: "/#/about"
  },
  {
    name: "PROJECTS",
    id: "projects",
    url: "/#/projects"
  }
];

export default {
  name: "PageLinks",
  methods: {
    updatePage(id) {
      this.$store.state.selectedPageId = id;
      this.setDefaultSelectedSection(id);
    },
    isSelected(id) {
      return this.$store.state.selectedPageId === id;
    },
    setDefaultSelectedSection(id) {
      this.$store.state.selectedSectionId = this.$store.state.pageSections[
        id
      ][0].id;
    }
  },
  data() {
    return {
      links
    };
  },
  created() {
    this.$store.state.selectedPageId = links[0].id;
  }
};
</script>

<style scoped>
@keyframes expand {
  from {
    height: 0;
  }
}

@keyframes slide-in {
  from {
    transform: translate(-100%);
  }
}

.pageLinks {
  position: absolute;
  left: 0;
  top: 60px;
  width: 90px;
  height: calc(100vh - 80px);
  background: rgb(42, 43, 56);
  z-index: 1;
  animation: slide-in 0.5s ease-out;
}
.pageLinks-item {
  position: relative;
  display: inline-block;
  width: 100%;
  padding-top: 100%;
  text-align: center;
  float: left;
}
.pageLinks-item:hover {
  background: rgba(0, 0, 0, 0.1);
}
.pageLinks-item.selected {
  background: rgb(26, 26, 35);
}
.pageLinks-item.selected::before {
  content: "";
  position: absolute;
  left: 0;
  top: 50%;
  transform: translate(0, -50%);
  height: 100%;
  width: 3px;
  background: rgb(102, 109, 180);
  animation: expand 0.3s ease-out;
}
.pageLinks-item-text {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  font-size: 12px;
  font-weight: bold;
  color: #ffffff;
  text-decoration: none;
}
</style>
